import { motion } from "framer-motion";
import React from "react";
import responsiveImg from "../img/responsive.jpg";
import "./../styles/Hero.css";
function Hero({ isShow, setShow }) {
  const showNavigation = (e) => {
    setShow(!isShow);
  };

  return (
    <motion.div
      onMouseOver={(e) => console.log(e)}
      animate={{ opacity: 1 }}
      initial={{ opacity: 0 }}
      transition={{ delay: 0.4 }}
    >
      {isShow ? (
        <div className="hero">
          <div className="hero-container">
            <div className="hero-content">
              <nav>
                <a href="/">Brand</a>
                <ul>
                  <li>Enquire Now</li>
                  <li onClick={showNavigation}>Menu</li>
                </ul>
              </nav>
              <div className="content-heading">
                <h1>KALPATARU AVANTE</h1>
                <h3>SANATH NAGAR</h3>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div className="responsive-nav">
          <a onClick={showNavigation} id="close-btn">
            Close
          </a>
          <motion.div
            className="nav-container"
            animate={{ opacity: 1 }}
            initial={{ opacity: 0 }}
            transition={{ delay: 0.4 }}
          >
            <div class="nav-body">
              <ul>
                <li>About</li>
                <li>Home</li>
                <li>Contact</li>
                <li>Company</li>
                <li>Etc.</li>
              </ul>
              <div className="nav-img-container">
                <img src={responsiveImg} />
              </div>
            </div>
          </motion.div>
        </div>
      )}
    </motion.div>
  );
}

export default Hero;
